import React, {createRef, useState} from "react";
import {useDispatch, useSelector} from 'react-redux';
import "./AddGiftAnotherStore.scss";
import Counter from "../../counter/Counter";
import TogglePills from "../toggle-pills/TogglePills";
import RadioButton from "../../radio-button/RadioButton";
import SelectCategory from "../../select-category/SelectCategory";
import {setAnotherStoreProductInfo} from "modules/my-registry/MyRegistryActions";
import {getScrapedProduct} from "api/axios";
import {toast} from 'react-toastify';
import CategoryPill from "../import-registry/category-pill/CategoryPill";
import defaultData from "data/ReducerDefaultData.json";

const AddGiftAnotherStore = (props) => {

  const MAX_IMAGE_SIZE = 5000000; // 5MB
  const ACCEPTED_IMAGE_TYPE = [
    'image/jpg',
    'image/jpeg',
    'image/png'
  ]

  const [categoryModalShow, setCategoryModalVisible] = useState(false);
  const [hideProductDetails, setHideProductDetails] = useState(true);
  const [showLoading, setshowLoading] = useState(false);

  const [btnHasFocus, setBtnHasFocus] = useState(false)
  const [inputHasFocus, setInputHasFocus] = useState(false)
  const [itemUrl, setItemUrl] = useState("")
  const [currentItemLink, setCurrentItemLink] = useState("")

  let inputRef = React.createRef()

  const dispatch = useDispatch();
  const productInfo = useSelector(state => state.myRegistry.anotherStoreProduct)

  const productImage = useSelector(state => state.myRegistry.anotherStoreProduct.productImage)
  const productName = useSelector(state => state.myRegistry.anotherStoreProduct.productName)
  const category = useSelector(state => state.myRegistry.anotherStoreProduct.category)
  const quantity = useSelector(state => state.myRegistry.anotherStoreProduct.quantity)
  const price = useSelector(state => state.myRegistry.anotherStoreProduct.price)
  const howToGift = useSelector(state => state.myRegistry.anotherStoreProduct.howToGift)
  const note = useSelector(state => state.myRegistry.anotherStoreProduct.note)
  const mustHave = useSelector(state => state.myRegistry.anotherStoreProduct.mustHave)
  const groupGift = useSelector(state => state.myRegistry.anotherStoreProduct.groupGift)

  const imageInput = createRef()

  const howToGiftOptions = [
    {
      id: 1,
      text: "Get cash equivalent for this gift"
    },
    {
      id: 2,
      text: "Direct guest to store website to buy and ship themselves"
    }
  ]

  // Update state function
  const changeProductUrl = (url) => {
    let prodInfo = {...productInfo};
    prodInfo.productUrl = url;
    dispatch(setAnotherStoreProductInfo(prodInfo));
  }

  const changeProductImage = (image) => {
    let prodInfo = {...productInfo};
    prodInfo.productImage = image;
    dispatch(setAnotherStoreProductInfo(prodInfo));
  }

  const changeProductName = (name) => {
    let prodInfo = {...productInfo};
    prodInfo.productName = name;
    dispatch(setAnotherStoreProductInfo(prodInfo));
  }

  const setCategory = (category) => {
    let prodInfo = {...productInfo};
    prodInfo.category = category;
    dispatch(setAnotherStoreProductInfo(prodInfo));
  }

  const setQuantity = (quantity) => {
    let prodInfo = {...productInfo};
    prodInfo.quantity = quantity;
    dispatch(setAnotherStoreProductInfo(prodInfo));
  }

  const changePrice = (price) => {
    let prodInfo = {...productInfo};
    prodInfo.price = price;
    dispatch(setAnotherStoreProductInfo(prodInfo));
  }

  const changeHowToGift = (howToGift) => {
    let prodInfo = {...productInfo};
    prodInfo.howToGift = howToGift;
    dispatch(setAnotherStoreProductInfo(prodInfo));
  }

  const changeNote = (note) => {
    let prodInfo = {...productInfo};
    prodInfo.note = note;
    dispatch(setAnotherStoreProductInfo(prodInfo));
  }

  const toggleMustHave = (mustHave) => {
    let prodInfo = {...productInfo};
    prodInfo.mustHave = mustHave;
    dispatch(setAnotherStoreProductInfo(prodInfo));
  }

  const toggleGroupGift = (groupGift) => {
    let prodInfo = {...productInfo};
    prodInfo.groupGift = groupGift;
    dispatch(setAnotherStoreProductInfo(prodInfo));
  }

  const openCategorySelect = (ev) => {
    ev.preventDefault();
    setCategoryModalVisible(true);
  }

  const incrementQuantity = () => {
    setQuantity(quantity + 1)
  }

  const decrementQuantity = () => {
    if (quantity - 1 > 0) {
      setQuantity(quantity - 1)
    }
  }

  const priceChangeHandler = (ev) => {
    ev.persist();

    if (ev.target.value.length > 0) {
      const newPrice = Number(ev.target.value);

      if (newPrice >= 0) {
        changePrice(ev.target.value);
      }
    } else {
      changePrice("")
    }
  }

  const productNameChangeHandler = (ev) => {
    ev.persist();
    changeProductName(ev.target.value)
  }

  const noteChangeHandler = (ev) => {
    ev.persist();
    changeNote(ev.target.value);
  }


  const selectFileHandler = (ev) => {
    if (imageInput.current && imageInput.current.files.length > 0) {
      const file = imageInput.current.files[0]

      // Verifying file is image
      if (ACCEPTED_IMAGE_TYPE.includes(file.type)) {
        if (file.size <= MAX_IMAGE_SIZE) {
          let reader = new FileReader();

          reader.onload = function (e) {
            const newProductImage = {
              type: "custom",
              value: e.target.result,
              file: file,
              fileType: file.type,
              fileName: file.name
            }
            changeProductImage(newProductImage);
          }

          reader.readAsDataURL(file); // Converting to base64 string
        } else {
          toast.error("File size exceeds the limit");
        }
      } else {
        toast.error("Please select valid image.");
      }
    }
  }

  const getRefinedPrice = (price) => {
    let newPrice = "";
    for (let char of price) {
      if (char !== ".") {
        if (Number.isInteger(Number(char))) {
          newPrice += char
        }
      } else {
        newPrice += char
      }
    }

    return newPrice;
  }

  const fetchProductDetail = (itemUrl) => {

    try {
      new URL(itemUrl);
    } catch {
      toast.error("Invalid Store Url")
      return;
    }
    setCurrentItemLink(itemUrl)
    changeProductUrl(itemUrl);
    const reqData = {
      item_url: itemUrl
    }
    let ctgry = category;
    let data = defaultData.myRegistry.defaultAnotherStoreProductInfo;
    data.category = ctgry
    dispatch(setAnotherStoreProductInfo(data))
    setshowLoading(true);
    setHideProductDetails(true);
    getScrapedProduct(reqData).then(res => {
      const resData = res.data;
      if (resData) {
        let prodInfo = {...productInfo};
        prodInfo.productUrl = itemUrl;
        prodInfo.productImage = {
          type: "url",
          value: resData.img
        };
        prodInfo.productName = resData.title;
        prodInfo.price = getRefinedPrice(resData.price);
        dispatch(setAnotherStoreProductInfo(prodInfo));
        setshowLoading(false);
        setHideProductDetails(false);
      }
    }).catch(err => {
      setshowLoading(false);
      toast.error("Invalid Store Url")
      return false;
    })
  }

  const checkUrlAndGetRegistryItems = () => {
    // Checking for valid url
    try {
      new URL(itemUrl);
    } catch {
      toast.error("Invalid Store Url")
      return;
    }

    setBtnHasFocus(false)
    fetchProductDetail(itemUrl)
  }

  const submitStoreUrl = (ev) => {
    ev.persist();
    ev.preventDefault();
    if (ev.keyCode === 13) { //Enter keycode is 13
      setBtnHasFocus(false)
      ev.target.blur()
      fetchProductDetail(itemUrl)
    }
  }

  const removeSelectedCategory = (categoryId) => {
    if (productInfo.category.length) {
      let finalcategory = productInfo.category.filter(category => category.id !== categoryId);
      let prodInfo = {...productInfo};
      prodInfo.category = finalcategory;
      dispatch(setAnotherStoreProductInfo(prodInfo));
    }
  }

  // Setting default HowToGiftOption
  if (!howToGift.id) {
    changeHowToGift(howToGiftOptions[1])
  }

  const changeItemUrl = (e) => {
    e.preventDefault();
    if (inputRef.current) {
      inputRef.current.disabled = false;
      inputRef.current.focus();
    }
  }

  return (
    <>
      <div className="row add-gift-another-store">
        <div className="col">
          <div className="row">
            <div className="col m-a-input-container">
              <p className="text-sm font-medium add-gift-store-label">Store</p>
              <div className="position-relative mt-1">
                <input type="text" className="w-100 pl-2 pr-14 py-1 text-body input-background change-link-input"
                       placeholder="Paste a product link that you want to add in your Monica and Andy Registry"
                       onKeyUp={(ev) => submitStoreUrl(ev)}
                       onChange={(ev) => setItemUrl(ev.target.value)}
                       onFocus={() => {
                         setInputHasFocus(true);
                         setBtnHasFocus(true)
                       }}
                       onBlur={() => setInputHasFocus(false)}
                       ref={inputRef}
                       value={itemUrl}
                />
                {/* <a className="text-sm" href="#">Change</a> */}
                {itemUrl.length > 0 && !inputHasFocus && !btnHasFocus?
                  <a tabindex
                     className="add-gift-another-store-change-link text-sm font-medium import-registry-category-link"
                     href="#"
                     onClick={(e) => changeItemUrl(e)}>Change</a> : null}

                {itemUrl.length > 0 && btnHasFocus ?
                  <div className="add-gift-another-store-submit-url-section">
                    {/*TODO: Change icons once added to global icons*/}
                    <button className="icon-close mr-3"
                            onClick={() => {
                              setItemUrl("");
                              setBtnHasFocus(false);
                            }}
                    />
                    <button className="icon-check" onClick={checkUrlAndGetRegistryItems}/>
                  </div>
                  : null}
              </div>
            </div>
          </div>

          <div className={["row mt-4", hideProductDetails ? "" : "pt-6"].join(" ")}>
            {hideProductDetails ? showLoading ?
              <div className="col text-center">
                <div className="hide-product-view py-13">
                  <div class="spinner-border" role="status">
                    <span class="sr-only">Loading...</span>
                  </div>
                  <h4 className="hide-product-details m-auto">Please wait while we fetch the product details</h4>
                </div>
              </div>
              :
              <div className="col text-center">
                <div className="hide-product-view py-13">
                  <h4 className="hide-product-details m-auto">Please paste product link above to view details here</h4>
                </div>
              </div>
              :
              <>
                <div className="col-12 col-md-5 add-gift-image-select">
                  <div className="mr-3">
                    {productImage?.value ?
                      <img
                        src={productImage.value}
                        alt="Product Image"
                      />
                      : null}

                    <i className="icon-preview p-1" onClick={() => {
                      imageInput.current.click()
                    }}/>
                    {/* TODO: Change above icon to camera icon */}

                    <input type="file" className="d-none" accept={ACCEPTED_IMAGE_TYPE.join(",")} ref={imageInput}
                           onChange={(ev) => selectFileHandler(ev)}/>
                  </div>
                </div>
                <div className="col-12 col-md-7">
                  <div className="ml-3">
                    <div className="row">
                      <div className="col m-a-input-container">
                        <p className="text-sm font-medium add-gift-store-label">Product Name</p>
                        <textarea
                          className="w-100 mt-1 h1 p-2"
                          rows="2"
                          value={productInfo.productName}
                          onChange={(ev) => productNameChangeHandler(ev)}
                        />
                      </div>
                    </div>

                    <div className="row mt-4">
                      <div className="col">
                        <div className="m-a-input-container mr-2">
                          <p className="text-sm font-medium add-gift-store-label">Price</p>
                          <div className="price-input mt-1">
                            <input
                              type="text"
                              className="w-100 pl-2 pr-1 py-1 text-body pl-5"
                              value={price}
                              onChange={(ev) => priceChangeHandler(ev)}
                            />
                          </div>
                          {/*<span className="price-input text-body">$</span>*/}
                        </div>
                      </div>
                      <div className="col">
                        <div className="m-a-input-container">
                          <p className="text-sm font-medium add-gift-store-label">Quantity</p>
                          <Counter
                            className="mt-2"
                            counter={quantity}
                            incrementHandler={incrementQuantity}
                            decrementHandler={decrementQuantity}
                          />
                        </div>
                      </div>
                    </div>

                    {!props.addMyOwn ?
                      <>
                        <div className="row mt-5">
                          <div className="col d-flex">
                            <p className="text-sm add-gift-store-label font-medium mb-1">Category</p>
                            <a className="text-sm ml-2 add-gift-link font-medium" href="#" tabindex="0"
                               onClick={(ev) => openCategorySelect(ev)}>Add</a>
                            {/* <div className="position-relative">
                            <input type="text" value={category.displayName} readOnly={true}
                                   className="w-100 pl-2 pr-12 py-1 text-body input-background change-link-input"/>
                          </div> */}
                          </div>
                        </div>
                        <div className="row mt-2">
                          <div className="col">
                            {category.map(cat => (
                              <CategoryPill
                                key={cat.id}
                                mainCategory={cat.mainCategory}
                                name={cat.categoryName}
                                removeCategory={() => removeSelectedCategory(cat.id)}
                                className="mt-2 mr-3"
                              />
                            ))}
                          </div>
                        </div>
                      </>
                      : null}

                    <div className="row mt-5">
                      <div className="col d-flex">
                        <TogglePills
                          iconClass="icon-heart"
                          activeClass={mustHave ? "active-must-have" : null}
                          onClick={() => toggleMustHave(!mustHave)}
                          text="Must Have"
                          className="mr-2"/>
                        <TogglePills
                          iconClass="icon-guest"
                          activeClass={groupGift ? "active-group-gift" : null}
                          onClick={() => toggleGroupGift(!groupGift)}
                          text="Group Gift"
                          className="mr-2"/>
                        {/* TODO: Change above guest icon one added */}
                      </div>
                    </div>

                    <div className="row mt-5">
                      <div className="col m-a-input-container">
                        <p className="text-sm font-medium add-gift-store-label">How to gift?</p>
                        {howToGiftOptions.map((option, index) => (
                          <div className={index === 0 ? "mt-1" : "mt-3"} key={"how-to-gift-option-" + option.id}>
                            <RadioButton
                              name="how-to-gift"
                              textClass="text-sm"
                              display={option.text}
                              checked={option.id === howToGift.id ? true : null}
                              changeHandler={() => changeHowToGift(option)}
                              centerText="false"/>
                          </div>
                        ))}
                      </div>
                    </div>

                    <div className="row mt-5">
                      <div className="col m-a-input-container">
                        <p className="text-sm font-medium add-gift-store-label">Notes</p>
                        <textarea className="w-100 mt-1 text-body p-1 m-a-resize-textarea mb-2"
                                  rows="2"
                                  placeholder="Write a note for your friends and family"
                                  value={note}
                                  onChange={(ev) => noteChangeHandler(ev)}
                        />
                      </div>
                    </div>
                  </div>
                </div>
              </>
            }
          </div>
        </div>
      </div>
      <SelectCategory
        showModal={categoryModalShow}
        closeModal={setCategoryModalVisible}
        currentCategory={category}
        changeCurrentCategory={setCategory}
      />
    </>
  )
}

export default AddGiftAnotherStore;