import React from "react";
import "./BookmarkletSection.scss";
import Modal from "../../modal-component/ModalComponent";
import Button from '../../button-component/ButtonComponent';
import { BOOKMARKLET_SCRIPT, BOOKMARKLET_STEP_THREE_PRODUCT_LINK, IMPORT_REGISTRY_EMAIL } from "utils/constants";

class BookmarkletSection extends React.Component {

  state = {
    show: false,
    updateModal: false,
  }

  modalTitle = ""
  modalBody = ""
  modalTitleContent = ""
  modalFooterButtons = ""

  // Event Handlers
  modalCloseHandler = () => {
    this.setState({
      show: false
    })
  }

  doneBookmarkletHandler = () => {
    this.modalCloseHandler()
  }

  openAddBookmarkletModal = () => {
    this.modalTitle = "Get M+A Button"
    this.modalTitleContent = (
      <div className="py-2 text-sm">
        It’s as easy as dragging the button to your browser’s Bookmarks bar.
      </div>
    )
    this.setState((prevState, props) => ({
      show: true,
    }))

    this.modalBody = (
      <div className="row bookmarlet-model justify-content-center">
        <div className="col">
          <div className="d-flex justify-content-center mb-6">
            <div className="text-body m-a-heading mr-5">
              <div>Drag the button the your</div> <div>browser’s Bookmarks bar</div>
            </div>
            <a
              tabindex="0"
              href={BOOKMARKLET_SCRIPT}
              className="m-a-btn m-a-primary-btn primary-btn px-3 py-2 font-weight-bold font-medium text-body"
            >Add to M+A</a>
          </div>
          <div className="bookmarklet-steps">
            <ol className="my-5">
              <li className="steps-title pl-4">
                <div className="text-body font-medium">Make sure your bookmarks bar is visible</div>
                <div className="mt-1 text-sm steps-details">
                  In Chrome browser, go to the top right menu and into Settings. On the left navigation you will find Appearance. Inside it find ‘Show Bookmarks’ bar then toggle to turn on.
                </div>
              </li>
              <li className="steps-title pl-4 mt-5">
                <div className="text-body font-medium">Add to your Bookmarks bar</div>
                <div className="mt-1 text-sm steps-details">
                  Click the “Add to M+A” button and drag it to your Bookmarks bar.
                </div>
              </li>
              <li className="steps-title pl-4 mt-5">
                <div className="text-body font-medium">Give it a go!</div>
                <div className="mt-1 text-sm steps-details">
                  Go to <a className="bookmarklet-link" href={BOOKMARKLET_STEP_THREE_PRODUCT_LINK} target='_blank' tabindex="0">Key Rattle</a> on Plan Toys and click on your NEW M+A button.
                </div>
              </li>
            </ol>
          </div>
        </div>
      </div>
    )
    this.modalFooterButtons = (
      <>
        <a tabindex="0" className="bookmarklet-link mr-auto" onClick={() => this.doneBookmarkletHandler()}>I Already Have M+A Button</a>
        <a
          className="px-3 py-1 bookmarklet-disable-btn text-body font-medium"
          href={"mailto:" + IMPORT_REGISTRY_EMAIL}
          target='_blank'
          tabindex="0"
        >Email Us For Help</a>
        <Button
          variant="primary"
          className="px-4 py-1 font-weight-bold font-medium text-body"
          onClick={() => this.doneBookmarkletHandler()}
        >Done</Button>
      </>
    )
  }

  render() {
    return (
      <>
        <div className="py-2 bookmarklet-section" onClick={this.openAddBookmarkletModal}>
          <div className="row">
            <div className="col flex-grow-0">
              {/* TODO: Add M$A Icon once added */}
            </div>
            <div className="col">
              <h4>Get M+A Button</h4>
              <p className="text-sm mt-1">M+A Button lets you add product from any store directly to Monica+Andy Baby
            Registry.</p>
              <a tabindex="0" className="bookmarklet-link mt-1 text-sm">I already have it</a>
            </div>
          </div>
        </div>
        <Modal
          show={this.state.show}
          updateModal={this.state.updateModal}
          title={this.modalTitle}
          titleContent={this.modalTitleContent}
          footer={this.modalFooterButtons}
          closeModal={this.modalCloseHandler}>
          {this.modalBody}
        </Modal>
      </>
    )
  }
}

export default BookmarkletSection;